.beats{
    max-width: 800px;
    margin: 0 auto;
    font-size: 1.1rem;
    padding: 1rem;
     margin-top: 3rem;
    background-color: #E8E9EB
}

.head{
    margin-bottom: 1rem;
    padding: .3rem;
    cursor: pointer;
    padding-bottom: .5rem;
    width: 100%;
    position: relative;
    -webkit-transition: all .3s ease-in;
    -o-transition: all .3s ease-in;
    transition: all .3s ease-in;
}

.head::before{
    content: '+';
    top: 0;
    position: absolute;;
    right: 0;
}


.head.active{
    -webkit-transition:all .3s ease-in;
    -o-transition:all .3s ease-in;
    transition:all .3s ease-in;
   background-color: #F7F7F7;
}




.items{
    margin-top: 1rem;
      padding: .3rem;
     -webkit-transform: translateY(-50%);
         -ms-transform: translateY(-50%);
             transform: translateY(-50%);
    -webkit-transition:all .3s ease-in;
    -o-transition:all .3s ease-in;
    transition:all .3s ease-in;
    padding-top: 1rem;
      padding-left: .5rem;
}
.items li{
    padding: .2rem;
    list-style: none;
}

.me {
  overflow: hidden;

  -webkit-transform: translateY(-11%);

      -ms-transform: translateY(-11%);

          transform: translateY(-11%);

  opacity: 0.7;
  margin-top: -1rem;
  -webkit-transition:all .3s ease-in;
  -o-transition:all .3s ease-in;
  transition:all .3s ease-in;
}
.me.open {
    background-color: #F7F7F7;
  padding: .1rem;
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);

}




